import dayjs from "dayjs";
export function createScript(src) {
    const script = document.createElement('script');
    script.setAttribute('src', src);
    return script;
}


export function createLink(href) {
    const link = document.createElement('link');
    link.setAttribute('rel', 'stylesheet');
    link.setAttribute('type', 'text/css');
    
    //  manifest  web_accessible_resources 
    link.setAttribute('href', href);
    link.setAttribute('crossorigin', 'anonymous');
    return link;
}


export function createOtherLink(keyword) {
    const decode = encodeURIComponent(keyword)
    const dom = document.createElement('div');
    dom.className = '__company_info';

    dom.innerHTML = `
        
        <a class="__company_info_name" href='https://www.xiaohongshu.com/search_result?keyword=${decode}' target="_blank" rel="noopener noreferrer">
            
        </a>
        &nbsp; | &nbsp;
        <a class="__company_info_name" href='https://maimai.cn/web/search_center?type=feed&query=${decode}&highlight=true' target="_blank" rel="noopener noreferrer">
            
        </a>
        &nbsp; | &nbsp;
        <a class="__company_info_name" href='https://www.baidu.com/s?wd=${decode}' target="_blank" rel="noopener noreferrer">
            
        </a>
    `

    return dom
}
 
 
// 
export function convertTimeToHumanReadable(dateTime) {
    let date = dayjs(dateTime);
    let curDate = dayjs();
	
	// 
	let minC = curDate.diff(date, 'minute', true);
	// 
	let hourC = curDate.diff(date, 'hour', true);
	// 
	let dayC = curDate.diff(date, 'day', true);
	// 
	let weekC = curDate.diff(date, 'week', true);
	// 
	let monthC = curDate.diff(date, 'month', true);
 
    if(minC < 5) {
        return ``;
    } else if (minC < 60) {
        return `1`;
    } else if (hourC < 24) {
        return `1`;
    } else if (dayC < 7) {
        return `${parseInt(dayC)}`
    } else if (monthC < 1) {
        return `${parseInt(Math.ceil(weekC))}`
    } else if (monthC <= 2) {
        return `2`
    } else if (monthC <= 3) {
        return `3`
    } else {
        return '3';
    }

}

export function convertTimeOffsetToHumanReadable(dateTime) {
    let date = dayjs(dateTime);
    let curDate = dayjs();
	
	// 
	let minC = curDate.diff(date, 'minute', true);
	// 
	let hourC = curDate.diff(date, 'hour', true);
	// 
	let dayC = curDate.diff(date, 'day', true);
	// 
	let monthC = curDate.diff(date, 'month', true);
    
    if(minC < 1){
        return ``;
    }else if (minC < 60) {
        return `${parseInt(minC)}`;
    } else if (hourC < 24) {
        return `${parseInt(hourC)}`;
    } else if (monthC < 1) {
        return `${parseInt(dayC)}`
    } else {
        return `${parseInt(monthC)}`
    }

}

export function getRandomInt(max) {
    return Math.floor(Math.random() * max);
}
 
export function debounce(fn, delay) {
    let timer = null;
    return function(...args) {
        if(timer) {
            clearTimeout(timer);
        }
        timer = setTimeout(() => {
            fn(...args);
        }, delay);
    }
}

// 
export function toHump(name) {
    return name.replace(/\_(\w)/g, function(all, letter){
        return letter.toUpperCase();
    });
}
// 
export function toLine(name) {
  return name.replace(/([A-Z])/g,"_$1").toLowerCase();
}

//  sleep 
export function delay(ms) {
    return new Promise((resolve) => setTimeout(resolve, ms));
}   